import './App.css';
import './Body.css'
import Header from './components/home_page/Header'
import Goals from './components/home_page/Goals'
import HomePageAbout from './components/home_page/HomePageAbout'
import TopCampaign from './components/home_page/TopCampaign'
import Founder from './components/home_page/Founder'
import Navigation from './components/Navigation/Navigation'
import Footer from './components/home_page/Footer'
import AlertMsg from './components/globals/AlertMsg'
import { useState } from 'react';

export default function App({ navigate }){
  const [alertModal, setAlertModal] = useState(false)

  const hideAlertModal = () => setAlertModal(false)

  const navigateTo = path => navigate(path)

  const onReachOut = () => setAlertModal({
    size: 'xl',
    msg: `We're currently working on the contact form, do check back later`,
    showModal: true,
    hideModal: hideAlertModal
  })

  return (
    <article>
      <Navigation navigate={navigate} />
      <Header navigateTo={navigateTo} />
      <Goals />
      <HomePageAbout />
      <TopCampaign />
      <Founder />
      <section className="main-contact p-lg-5 p-4 py-5 d-flex flex-wrap justify-content-around align-items-center">
        <div className="col-lg-7 col-12">
          <h1 className="main-contact-title mb-5">Let's build a Community of Leaders</h1>
          <p className="main-contact-text">
            A fast developing charity organization. Helping street children is the goal. 
            Driven by an undying desire to express concern towards those whom a greate half 
            of the society have turned their backs on.           
          </p>
        </div>
        <div className="col-lg-4 px-5">
          <button onClick={onReachOut} 
          className="px-lg-5 py-lg-3 px-md-3 py-md-1 px-3 py-1 justify-content-center main-contact-btn">REACH OUT</button>
        </div>
      </section>
      <Footer navigate={navigate} />

      {
        alertModal && <AlertMsg alertModal={alertModal} />
      }

      {/* Edutainment */}

      {/* <Edutainment socket={socket} userDetails={userDetails} currentQuestion={currentQuestion} />
      <button onClick={() => navigateTo('challenges')}>Challenge</button> */}
    </article>
  )
}