import { Routes, Route, useNavigate, useLocation } from 'react-router-dom'
import App from './App'
import About from './components/about/About'
import Campaign from './components/campaign/Campaign'
import Contact from './components/contact/Contact'
import Donate from './components/donations/Donate'

export default function Routing(){
    const navigator = useNavigate()

    const navigate = path => path == '' ? navigator('/') : navigator(`/app/${path}`)

    return (
        <Routes>
            <Route path="/" element={<App navigate={navigate} />} />

            <Route path="/app/about" element={<About navigate={navigate} />} />

            <Route path="/app/campaigns" element={<Campaign navigate={navigate} />} />

            <Route path="/app/contact" element={<Contact />} />

            <Route path="/app/donate" element={<Donate />} />

            <Route path='*' element={
                <div className='d-flex justify-content-center'>
                    <h1>Page Not Found</h1>
                </div>
            } />
        </Routes>
    )
}